.PC_wrap {
  /* background-color: rgb(255, 174, 0); */
  display: flex;
  flex-direction: column;
  color: black;
  text-decoration: none;
  margin-bottom: 5px;
  width: 100%;
  height: 0;
  padding-top: 165%;
  position: relative;
  border: 1px red solid;
  border: 1px transparent solid;
  /* border-radius: 10px; */
}

.PC_ratio {
  /* background-color: rgb(0, 153, 255); */
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.PC_wrap:hover {
  /* color: black; */
  border: 1px solid black;
  /* border-radius: 10px; */
}

.PC_picture_wrap {
  /* background-color: red; */
  width: 100%;
  height: 83%;
}

.PC_picture {
  /* border-radius: 10px; */
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.PC_txt_wrap {
  /* background-color: green; */
  width: calc(100% - 0px);
  height: calc(100% - 83% - 0px);

  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  /* background-color: transparent; */
}

.PC_txt {
  /* background-color: aqua; */
  padding: 0;
  margin: 0;
  font-size: calc(0.8em + 0.5vw);
  font-style: italic;
}