.wrapper {

    margin-top: 10px;
    display: flex;
    flex-flow: column;
    align-items: center;
    /* background-color: rgb(129, 218, 77); */
    height: 700px;
    width: 80%;
    margin: auto;

    position: relative;
    top: 0;
    left: 0;
}

.square {
    width: 200px;
    height: 500px;
}


.div1 {
    height: 400px;
    width: 300px;
    background-color: red;
    transform: translateY(-800px);
    transition: all 1s;
    opacity: 0;
    margin: 20px;
}

.div2 {
    height: 400px;
    width: 300px;
    background-color: red;
    transform: translateX(0);
    transition: all 1s;
    opacity: 0.8;
    margin: 20px;
}

.box1_1 {
    position: absolute;
    top: 40%;
    left: 35%;

    transform: translateY(0);
    transition: all 1s;
    opacity: 1;


    background-color: transparent;
}

.box1_2 {
    position: absolute;
    top: 40%;
    left: 35%;

    transform: translateY(800px);
    transition: all 1s;
    opacity: 0;


    background-color: transparent;
}

.box1_txt {

    color: black;
    font-size: 1.8em;
    text-align: center;
}





.box2_1 {
    position: absolute;
    top: 0;
    left: 30%;
    right: 30%;
    bottom: 70%;

    transform: translateX(0) translateY(0);
    /* transform: ; */
    transition: all 1s;
    opacity: 1;


