.News_main {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    margin: 60px auto;
}

.News_square_wrap {
    display: flex;
    flex-direction: row;
    width: 75%;
    margin: 20px auto;
    /* border: 5px solid black; */
    justify-content: space-between;
}
.News_square_wrap2 {
    display: flex;
    flex-direction: row-reverse;
    width: 75%;
    margin: 20px auto 50px;
    /* border: 5px solid black; */
    justify-content: space-between;
}

.News_square {
    width: 14%;
    padding-bottom: 8%;
    height: 0;
}

.News_square_v2 {
    width: 14%;
    padding-bottom: 2%;
    height: 0;
}

.News_square1 {
    background-color: rgb(247, 247, 245);
}

.News_square2 {
    background-color: rgb(250, 216, 171);
}

.News_square3 {
    background-color: rgb(201, 134, 84);
}

.News_square4 {
    background-color: rgb(148, 104, 62);
}

.News_square5 {
    background-color: rgb(103, 45, 17);
}

.News_square6 {
    background-color: rgb(76, 43, 23);
}

.News_title {
    /* color: rgb(178, 169, 154); */
    color: rgb(148, 104, 62);
    font-family: 'Roboto Slab', serif;
    font-size: 2em;
    font-weight: 400;

}

.News_Block_Image_wrap {
    width: 92%;
    margin: 0 auto 20px;
    /* border: 2px solid black; */
    height: 0;
    padding-bottom: 42%;
    position: relative;
    /* background-image: url('../../assets/aide/paint.png'); */
    /* background-repeat: no-repeat; */
}

.News_Block_Image {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.News_img_fixed_1 {
    height: 40%;
    width: 15%;
    position: absolute;
    top: 26%;
    left: 0;
    object-fit: contain;
}

.News_img_fixed_2 {
    height: 41%;
    width: 20%;
    position: absolute;
    bottom: 4%;
    left: 25%;
    object-fit: cover;
}
.News_txt_fixed_2 {
    width: calc(14% - 20px);
    text-align: center;
    position: absolute;
    top: 55%;
    left: 31%;
    background-color: rgba(255, 255, 255, 0.6);
    padding: 10px;
    margin: 0;
    font-family: 'Dosis', sans-serif;
    font-weight: 600;
    font-size: calc(0.7em + 1vw) ;
}

.News_img_fixed_3 {
    height: 31%;
    width: 14%;
    position: absolute;
    top: 5%;
    right: 17%;
    object-fit: cover;
}

.News_txt_fixed_3 {
    width: 14%;
    text-align: center;
    position: absolute;
    bottom: 95%;
    left: 69%;
    /* background-color: aqua; */
    margin: 0;
    font-family: 'Dosis', sans-serif;
    font-weight: 600;
}

.News_img_fixed_4 {
    height: 41%;
    width: 16%;
    position: absolute;
    top: 27%;
    right: 0;
    object-fit: cover;
}

.News_img_1 {
    height: 64%;
