@media (min-width: 510px) {
  .CC_main_wrap {
    margin: 60px 0;
  }

  .CC_top {
    /* background-color: aqua; */
  }

  .CC_top_titre {
    /* background-color: orange; */
    width: 50%;
    margin: auto;
    text-align: center;
  }

  .CC_top_txt {
    /* background-color: orangered; */
