    /* text-align: center; */
  }

  .AccueilContainerSubContent2 {
    /* background-color: burlywood; */
    text-align: center;
    margin: 20px auto;
    width: min(70%, 1200px);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .AccueilContainerCorps_picture {
    height: auto;
    max-width: 40vw;
  }

  .CardProduct_wrap1 {
    margin: 15px 1%;
    display: flex;
    flex-direction: row;
    width: max(23%, 200px);
    /* background-color: rgb(255, 127, 206); */
  }


  .oo_txt_wrap {
    width: 90%;
    margin: auto;
    font-weight: 500;
  }

  .oo_txt1 {
    color: black;
    font-size: 1.2em;
  }

  .oo_txt2 {
    color: black;
    margin-top: 8px;
  }

  .oo_link {
    appearance: none;
