.Guide_main {

    color: white;
    background-color: black;

}


.Guide_part1_bandeau_blanc {
    width: 100%;
    background-color: white;
    color: black;
    text-align: center;
    padding: 30px;
    font-size: 2em;
    font-family: 'Courgette', cursive;
}

.Guide_part1_image_wrap {
    width: 100%;
    position: relative;
}

.Guide_part1_image {
    width: 100%;
}

.Guide_part1_texte_wrap {
    padding: 10px 35px;
    font-size: 1.1em;
}





.Guide_block1_wrap {
    width: 90%;
    margin: 40px auto;
    padding: 20px 0;
    font-size: 1.1em;
}

.Guide_block1 {
    width: 65%;
    font-size: 1.1em;
}

.Guide_img1_wrap {
    background-color: white;
    width: 25%;
    float: right;
    height: 0;
    padding-bottom: 32%;
    position: relative;
    transform: rotate(15deg);
    margin: 0 50px;
    margin-top: 50px;
}

.Guide_img1_wrap2 {
    position: absolute;
    top: 5%;
    left: 5%;
    right: 5%;
    bottom: 12%;
    /* background-color: aqua; */
}

.Guide_img1 {
    display: inline-block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}


.Guide_block2_wrap {
    width: 90%;
    margin: 40px auto;
    padding: 20px 0;
    font-size: 1.1em;
}

.Guide_block2 {
    width: 75%;
    margin-left: 25%;
    font-size: 1.1em;
}


.Guide_block3_wrap {
    width: 90%;
    margin: 40px auto;
    padding: 20px 0;
    font-size: 1.1em;
}

.Guide_block3 {
    width: 65%;
    font-size: 1.1em;
}

.Guide_img3_wrap {
    background-color: white;
    width: 25%;
    float: right;
    height: 0;
    padding-bottom: 32%;
    position: relative;
    transform: rotate(15deg);
    margin: 0 50px;
    margin-top: 50px;
}

.Guide_img3_wrap2 {
    position: absolute;
    top: 5%;
    left: 5%;
    right: 5%;
    bottom: 12%;
    background-color: aqua;
}

.Guide_img3 {
    display: inline-block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@keyframes twinkle {
    40% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}


.special_border {
    border: 50px solid transparent;
    padding: 15px;
    background-color: rgb(0, 47, 255);
    background-color: transparent;
    border-image: url(../../assets/aide/border.png) 130 round;
    /* color: rgb(0, 90, 90) */
}


.Guide_arbre_link {
    appearance: none;
    cursor: pointer;
    /* text-decoration: none; */
    color: white;
    font-weight: 600;
    font-size: 1.1em;
}