.Collapsible_title_wrap {
    /* background-color: darkcyan; */
    /* border-top: 1px solid black; */
    border-bottom: 1px solid rgb(167, 167, 167);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    color: black;
    cursor: pointer;
}

.Collapsible_title_wrap:hover .Collapsible_title_part_icc {
    animation: icc 1.5s infinite linear;
}

@keyframes icc {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.Collapsible_title_part {
    margin: 5px 0px;
    font-size: 1em;
    color: black;
    /* background-color: aqua; */
}

.Collapsible_title_part_icc {
    color: rgb(167, 167, 167);
    padding: 0 10px;
    /* background-color: aqua; */
}